$top-color:#0C630C;
$base-text:#000000;
$sec-text:#ffffff;
$title-color:#363333;
$background:#EADFD4;

*{
  padding: 0;
  margin: 0;
}
a{
  text-decoration: none;
}
body{
  background-color: $background;
}

.flex-row{
  display: flex;
  flex-direction: row;
}
.flex-column{
  display: flex;
  flex-direction: column;

}

.flex-center{
  justify-content: center;
  align-items: center;
}


//Navbar
.navbar{
  background-color: $top-color;
  display: flex;
  flex-direction: column;

  img{
    margin: 10px;
    width: 50px;
  }
  .flex-row{
    align-items: center;
  }
  .menu{
    justify-content: space-around;

    h2{
      padding:10px;
      color:$base-text;
    }
  }
}


//Home page

.home{
  background-color: $background;
  padding: 10px;

  h1{
    color: $title-color;
  }
  .list-item{
        border-style: solid none ;
        border-color: white;
        border-width: 1px;


    .title{
        color: $title-color;
    }
    .btn{
      background-color: $top-color;
      color:$sec-text;
      border-radius: 20px;
      border: none;
      margin: 10px auto;
      padding: 10px 30px;
      font-size: 15px;


      a{
        color:white;
        text-decoration: none;
      }
    }
  }
}




//Add Link page

.AddLink{
  padding: 10px;


  h1{
    color: $title-color;
  }
  h4{
    margin: 10px;
    color:red;
  }

  form{
    display: flex;
    flex-direction: column;

    input{
      padding: 5px;
      font-size: 15px;
      margin: 10px;
      color:$title-color ; 
      border-radius:5px;
      border:none;
      outline: $top-color;
    }
    select{
      font-size: 15px;
      padding: 10px;
      margin: 10px;
      color: $title-color; 
      border-radius:5px;
    }

    .submitBtn{
      background-color: $top-color;
      color: $sec-text;
    }
    
  }

}